$browser-default-font-size: 16px !default; //

//$px為需要轉換的數字
@function pxTorem($px) {
  @return $px / $browser-default-font-size * 1rem;
}
//$rem為需要轉換的數字
@function remtopx($rem) {
  @return $rem / 1rem * $browser-default-font-size;
}
