@import "_config";

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh; /* Fallback for browsers that do not support Custom Properties */
  height: calc(var(--vh, 1vh) * 100);
  width: 100vw;
  background-color: $primary-color;
  padding: $gap;
  position: relative;

  .side-bar {
    height: 100%;
    width: 350px;
    margin-right: $gap;
    border-radius: $rounded-rate;
    overflow: hidden;
    transition: all 0.25s ease-in-out;
    display: grid;
    grid-template-rows: repeat(4, auto);

    .nav-bar {
      height: $nav-h;
      margin-bottom: $gap;
      border-radius: $rounded-rate;
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: $primary-color;

      nav {
        background-color: $bg-1;
        border-radius: $rounded-rate;
        height: 100%;
        width: 92%;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: $gap;

        .office-decks {
          background-color: $bg-1;
          height: 100%;
          border: 0.1px solid black;
          width: 50%;
          border-radius: $rounded-rate * 0.75;
          cursor: pointer;
        }
        .keyword-search {
          background-color: $btn-1;
          height: 100%;
          border: 0.1px solid black;
          width: 50%;
          border-radius: $rounded-rate * 0.75;
          cursor: pointer;
        }
      }
      .menu-btn {
        background-color: $bg-1;
        border-radius: $rounded-rate;
        margin-left: $gap;
        height: 100%;
        width: 8%;
        min-width: $btn-h;
        cursor: pointer;
      }
    }
    .selector {
      height: $selector-h;
      margin-bottom: $gap;
      border-top-left-radius: $rounded-rate;
      border-top-right-radius: $rounded-rate;
      border-radius: $rounded-rate;
      transition: all 0.25s ease-in-out;
      overflow: hidden;

      .selector-nav {
        background-color: $bg-1;
        height: $selector-h;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: $gap;
        cursor: pointer;

        .selector-title {
          width: 95%;
          height: 100%;
          padding: $gap 0;
        }
        .drop-btn {
          border: 0.1px solid black;
          border-radius: $rounded-rate;
          height: $btn-h;
          min-width: $btn-h;
        }
      }
      .selector-nav:hover {
        .drop-btn {
          background-color: $bg-3;
        }
      }

      .keyword-search {
        height: 100%;
        background-color: $bg-1;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: $gap;
        input {
          min-height: $filter-card-h;
          width: 88%;
          background: rgba(136, 136, 136, 0.0666666667);
          border-radius: $rounded-rate;
          border: none;
        }

        .icon {
          min-height: $filter-card-h;
          min-width: $filter-card-h;
          display: flex;
          justify-content: center;
          align-items: center;
          font-size: large;
          margin-left: $gap;
          border-radius: $rounded-rate;
        }
        .icon:hover {
          background-color: $bg-3;
        }
      }

      .selector-drop {
        height: $selector-drop-h;
        overflow: auto;

        .item {
          height: 65px;
          background-color: $bg-1;
          border-bottom: 0.1px solid black;
        }
        .item:first-child {
          box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.07) inset;
        }
      }
    }
    .search {
      padding: $gap;
      height: $search-nav-h;
      transition: all 0.25s ease-in-out;
      border-top-left-radius: $rounded-rate;
      border-top-right-radius: $rounded-rate;
      overflow: hidden;
      background-color: $bg-1;
      .search-nav {
        border-top-left-radius: $rounded-rate;
        border-top-right-radius: $rounded-rate;
        height: $btn-h;
        background-color: $bg-1;
        display: flex;
        justify-content: space-between;
        align-items: center;

        .search-title {
          height: 100%;
          width: $title-w;
        }

        .btn-box {
          height: 100%;
          width: 3 * $btn-h + remtopx($gap);
          display: flex;
          justify-content: center;
          .filter-btn {
            background-color: $bg-3;
            border: 0.1px solid black;
            height: $btn-h;
            width: $btn-h;
            border-radius: $rounded-rate;
            cursor: pointer;
          }
          .filter-btn:nth-child(2) {
            margin: 0 0.5 * $gap;
          }
          .filter-btn:hover {
            background-color: $btn-1;
          }
        }
      }
      .search-drop {
        height: $search-drop-h + remtopx(2 * $gap);
        transition: all 0.25s ease-in-out;
        display: grid;
        grid-template-columns: repeat(11, 1fr);
        overflow: auto;
        margin: $gap 0 0 0;

        .filter-card {
          border: 0.1px solid black;
          transition: all 0.25s ease-in-out;
        }
      }
    }
    .search-display {
      background-color: $bg-3;
      box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.07) inset;
      display: grid;
      grid-template-columns: repeat(3, auto);
      padding: $gap;
      overflow: auto;
      overflow: auto;

      .search-card {
        height: $search-card-h;
        border: 0.1px solid black;
      }
    }
  }

  // 下拉框顯示
  .selector-drop-show {
    .selector {
      height: $selector-h + $selector-drop-h;
    }
  }

  .search-drop-show {
    .search {
      height: $search-nav-h + $search-drop-h + remtopx(3 * $gap);
    }
  }

  .card-build {
    background-color: $bg-3;
    height: 100%;
    width: 85vw;
    border-radius: $rounded-rate;
    transition: all 0.5s ease-in-out;
    overflow: hidden;

    .card-nav {
      height: $search-nav-h;
      background-color: $bg-1;
      box-shadow: 0 4px 10px rgba(0, 0, 0, 0.07);
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: $gap;

      .card-menu {
        height: 100%;
        width: $title-w + $btn-h + remtopx($gap);
        display: flex;
        justify-content: center;
        align-items: center;
        .card-nav-title {
          width: $title-w;
          height: 100%;
          margin-left: $gap;
        }
      }

      .card-btn-box {
        height: 100%;
        width: 4 * $btn-h + remtopx(3 * $gap);
        display: flex;
        justify-content: space-between;
        align-items: center;
      }

      .card-nav-btn {
        height: $btn-h;
        min-width: $btn-h;
        background-color: $bg-3;
        border-radius: $rounded-rate;
        cursor: pointer;
      }
      .card-nav-btn:hover {
        background-color: $btn-1;
      }
    }

    .card-area {
      // border: 0.1px solid black;
    }
  }
}
/* 螢幕寬度介於 769px ~ 992px 時，就會執行 */
@media (min-width: 769px) {
  .container {
    .side-bar {
      .nav-bar {
        nav {
          width: 100%;
        }
        .menu-btn {
          display: none;
        }
      }
    }
  }
}

/* 螢幕寬度介於 769px ~ 992px 時，就會執行 */
@media (min-width: 769px) and (max-width: 993px) {
  .container {
    .side-bar {
      height: 100%;
      width: 280px;

      .search {
        .search-drop {
          grid-template-columns: repeat(6, 1fr);
        }
      }
    }
    .card-build {
      height: 100%;
      width: 85vw;
    }
  }
}

/* 螢幕寬度低於768px 時，就會執行 */
@media (max-width: 768px) {
  .container {
    .side-bar {
      height: 100%;
      width: 100vw;
      margin-right: 0;

      nav {
        width: 88%;
      }

      .menu-btn {
        display: block;
      }

      .search-display {
        grid-template-columns: repeat(4, auto);
      }
    }
    .card-build {
      height: 100%;
      width: 100vw;
    }

    .slide-hidden {
      width: 0;
    }
  }
}
/* 螢幕寬度介於 577px ~ 768px 時，就會執行 */
@media (min-width: 577px) and (max-width: 768px) {
  .container {
    .side-bar {
      .search {
        .search-drop {
          grid-template-columns: repeat(11, 1fr);
        }
      }

      .search-display {
        grid-template-columns: repeat(4, 1fr);
      }
    }
  }
}

/* 螢幕寬度介於 380px ~ 576px 時，就會執行 */
@media (min-width: 380px) and (max-width: 576px) {
  .container {
    .side-bar {
      .search {
        .search-drop {
          grid-template-columns: repeat(8, 1fr);
        }
      }

      .search-display {
        grid-template-columns: repeat(3, 1fr);
      }
    }
  }
}

/* 螢幕寬度小於 380px時，就會執行 */
@media (max-width: 380px) {
  .container {
    .side-bar {
      .search {
        .search-drop {
          grid-template-columns: repeat(6, 1fr);
        }
      }

      .search-display {
        grid-template-columns: repeat(2, 1fr);
      }
    }
  }
}
